<template>
  <div>
    <dl>
      <template v-for="(val, key ) in settings" > 
        <dt :key="key + 'a'">{{ optionDesc[key] ? optionDesc[key] + ' * ' + key : key }}</dt>
        <dd :key="key + 'b'">

          <div v-if="val == 'true' || val == 'false'">
            <label><input type="radio" v-model="settings[key]" value="true"> 是 </label>
            <label><input type="radio" v-model="settings[key]" value="false"> 否 </label>

          </div>
          <div v-else-if="/^[0-9]+$/.test(val)">
            <input type="number" v-model="settings[key]">
            <!-- <input type="text" :value="val"> -->
          </div>
          <div v-else>
            <input type="text" v-model="settings[key]">

          </div>

        </dd>
      </template>
    </dl>
    <button @click="changeGlobalOption">保存</button>
  </div>
</template>

<script>
import fanyi from './fanyi.js'
// 选项翻译
let optionDesc = fanyi.aa
console.log('dd',optionDesc)

export default {
  name:'Settings',
   props:['aria2'],
  data() {
    return {
      settings: null,
      optionDesc:optionDesc,
    }
  },
  async mounted() {
    this.settings = await this.aria2.getGlobalOption()
  },
  methods: {
    async changeGlobalOption() {
      await this.aria2.changeGlobalOption(this.settings)
      // 有些是更改不成功的. 重新加载一次刷新
      this.settings = await this.aria2.getGlobalOption()
    }
  }
}
</script>
